.team {
  background-image: url('../img/bg/team-bg.jpg');
  @include cover;
  justify-content: flex-end;
}

.team__wrapper {
  flex-basis: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(47,50,52,.93);

  @include tablets {
    flex-basis: 100%;
  }
}

.team__inner {
  width: 64%;
  margin-top: 25%;

  @include tablets {
    text-align: center;
  }
}

.worker {
  font-size: rem(14px);
  margin-bottom: rem(20px);

  @include tablets {
    margin-left: rem(-10px);
  }

  &__name-link {
    @include gagalin;
    display: inline-block;
    position: relative;
    margin-bottom: rem(15px);

    @include tablets {
      font-size: rem(24px);
    }

    &::before {
      content: '';
      display: inline-block;
      vertical-align: middle;
      border: 5px solid transparent;
      border-top: 5px solid $yellow;
      margin-right: rem(10px);
      transition: .5s;
    }
  } 

  &__content {
    overflow: hidden;
    height: 0;
    padding-left: rem(20px);
    transition: .5s;
  }

  &__content-wrapp {
    display: flex;

    @include tablets {
      padding: 0;
      flex-direction: column;
      align-items: center;
    }
  }

  &__photo {
    @include circle(rem(100px), $white);
    @include cover;
    flex-shrink: 0;

    @include tablets {
      margin-bottom: rem(20px);
    }
  }

  &__desc {
    color: $white;
    margin-left: rem(20px);

    @include tablets {
      margin-left: 0;
    }
  }

  &__head {
    font-size: rem(14px);
    font-weight: $boldFont;
    margin-bottom: rem(15px);
  }

  &__text {
    font-weight: $lightFont;
  }
}

.worker.is-active {

  .worker__name-link {
    color: $yellow;

    &::before {
      transform: rotate(180deg);
      vertical-align: unset;
    }
  }
}