.reviews {
  background: #000;

  &__list {
    @include size;
    display: flex;
    flex-wrap: wrap;
  }

  &__item {
    @include size(25%, 50%);

    @include tablets {
      @include size(50%, 25%);
    }
  }
}

.review {
  @include size;
  position: relative;

  &__photo {
    @include size;
    @include cover;
    filter: grayscale(1);
  }

  &__content {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    padding: 0 7%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: $white;
    font-size: rem(14px);
    font-weight: $lightFont;
    opacity: 0;
    transition: .4s;

    @include tablets {
      align-items: center;
    }

    &:hover {
      opacity: 1;
      background-color: rgba(22, 22, 22, 0.7);
    }
  }

  &__head {
    @include gagalin;
    font-size: rem(14px);
    margin-bottom: 1.5rem;

    @include phones {
      font-size: rem(24px);
      text-align: center;
    }
  }

  &__text {
    line-height: 1.7;
    margin-bottom: 1rem;

    @include phones {
      display: none;
    }
  }
}

.review__open {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba($grey, 0.9);
}

.review__window {
  position: relative;
  background-color: #fff;
  width: 90%;
  border-radius: 5px;
  color: $grey;
  font-size: rem(14px);
  padding: 1.5rem;
}

.review__head-hide {
  @include gagalin;
  font-size: rem(14px);
  margin-bottom: 1rem;
}

.review__text-hide {
  line-height: 1.7;
}

