.burger {
  display: flex;
  align-items: center;
  padding: 0 1rem;

  @include tablets {
    flex-direction: column;
  }

  &__photo {
    flex: 1;
    position: relative;
    padding: rem(80px) 0;

    @include tablets {
      width: 100%;
      text-align: center;
      padding-bottom: 0;
    }
  }

  &__img {
    max-width: 100%;
  }

  &__desc {
    display: flex;
    flex-direction: column;
    flex: 1;
    margin-left: 1rem;

    @include tablets {
      width: 100%;
      text-align: center;
      margin: 0;
    }
  }

  &__text {
    font-size: rem(14px);
    color: $white;
    line-height: 1.7;
  }

  &__order {
    display: flex;
    align-items: baseline;

    @include tablets {
      justify-content: center;
    }
  }

  &__price {
    font-family: gagalin;
    color: $orange;
    margin-right: rem(30px);

    &-num {
      font-size: rem(71px);

      @include small-phones {
        font-size: rem(60px);
      }
    }

    &-text {
      font-size: rem(30px);
    }
  }
}

.burger__badge {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  left: rem(30px);

  &--ingred {
    top: 0;
    background-color: #eb8c38;
    padding: rem(14px) rem(23px);
    border-radius: 5px;
    cursor: pointer;
    transition: .3s;

    &:hover {
      background-color: $orange;
    }
  }

  &--calor {
    bottom: 1rem;

    @include tablets {
      top: .5rem;
      right: 2rem;
      bottom: auto;
      left: auto;
    }
  }
}

.burger__badge-text {
  font-family: gagalin;
  font-size: rem(14px);
  color: $white;

  &--op {
    opacity: .5;
  }

  &--num {
    position: absolute;
    @include centered;
    font-size: rem(20px);
  }
}

.burger__ingred-svg {
  width: rem(45px);
  height: rem(45px);
  fill: #FFFFFF;
}

.burger__calor-svg {
  width: rem(60px);
  height: rem(60px);
  fill: rgba(255, 255, 255, 0.5);
}

.burger__dropdown {
  position: absolute;
  top: 0;
  left: 100%;
  background-color: $white;
  border-radius: 5px;
  transition: 1s;
}