*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  font-size: 16px;

  @include laptop {
    font-size: 15px;
  }

  @include tablets {
    font-size: 14px;
  }

  @include phones {
    font-size: 13px;
  }

  @include small-phones {
    font-size: 12px;
  }
}

html,
body {
  @include size;
  margin: 0;
  padding: 0;
}

body {
  @include ubuntu($regularFont);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $grey;
  line-height: 1.42;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  margin: 0;
}

a {
  color: $white;
  text-decoration: none;
}

p {
  margin: 0;
  padding: 0;
}

.wrapper {
  @include size;
  position: relative;
  //overflow: hidden;
}

.main-content {
  height: 100%;
  min-height: rem(650px);
}

.page {
  height: 100%;
  min-height: rem(650px);
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  overflow: hidden;
}

.container {
  width: calc(100% - 3rem);
  max-width: rem(940px);
  margin: 0 auto;  
  display: flex;
  flex-direction: column;

  &--footer {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    @include phones {
      flex-direction: column;
    }
  }

  &--center {
    justify-content: center;
  }
}

.visually-hidden { 
  clip: rect(1px, 1px, 1px, 1px); 
  height: 1px; 
  overflow: hidden; 
  position: absolute; 
  white-space: nowrap; 
  width: 1px; 
}