.intro {
  background-image: url('../img/bg/intro-bg.jpg');
  @include cover();

  &__content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
    padding-left: rem(20px);
    padding-bottom: rem(38px);

    @include tablets {
      flex-direction: column;
      padding: 0;
    }
  }

  &__first {
    width: 50%;

    @include tablets {
      width: auto;  
    }
  }

  &__second {
    @extend .intro__first; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    
    @include tablets {
      width: 80%;
      text-align: center;
    }

    @include phones {
      width: 100%;
    }
  }

  &__img {
    max-width: 100%;
    max-height: 100%;
    padding-bottom: rem(63px);

    @include phones {
      padding: 0;
    }
  }

  &__slogan {
    @include lifehack;
    font-size: rem(65px);
    line-height: 1;
    color: $white;

    @include small-phones {
      font-size: rem(45px);
    }
  }

  &__buttons {
    margin-bottom: 1rem;
    align-self: center;
  }
}