.btn {
  font-size: rem(13px);
  color: $white;
  border: none;
  text-transform: uppercase;
  border-radius: rem(5px);
  padding: rem(10px) rem(16px);
  cursor: pointer;
  transition: 0.3s;
  outline: none;
  background-color: $orange;

  &:hover {
    background-color: darken($orange, 7%);
  }
}

.btn--review {
  background-color: $grey;
  align-self: flex-start;

  @include tablets {
    align-self: center;
  }

  @include phones {
    display: none;
  }

  &:hover {
    background-color: darken($grey, 7%);
  }

  &-phones {
    display: none;

    @include phones {
      display: block;
    }
  }
}