.modal__close {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 20px;
  z-index: 999;

  &--ingred {
    display: none;

    @include tablets {
    display: block;
    }
  }

  &::before,
  &::after {
    content: '';
    position: absolute;
    top: 8px;
    width: 20px;
    height: 4px;
    background-color: $orange;
    border-radius: 4px;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}