.nav-trigger {
  display: none;
  position: fixed;
  top: rem(32px);
  right: rem(28px);
  z-index: 999;

  @include tablets {
    display: block;  
  }
  
  &__link {
    display: block;
    width: 25px;
    height: 25px;
  }

  &__badge {

    &,
    &::before,
    &::after {
      display: block;
      height: 4px;
      width: 25px;
      border-radius: 4px;
      background-color: $white;
    }

    &::after,
    &::before {
      content: '';
      position: absolute;
    }

    &::before {
      top: 9px;
    }

    &::after {
      top: 18px;
    }
  }

  &.is-active {

    .nav-trigger__badge {
      background-color: transparent;

      &::before {
        transform: rotate(45deg);
      }

      &::after {
        transform: translateY(-9px) rotate(-45deg);
      }
    }
  }
}

