.best {
  background-image: url('../img/bg/best-bg.jpg');
  @include cover;
  text-align: center;
  align-items: center;

  &__list {
    display: flex;

    @include phones {
      flex-direction: column;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    flex: 1;
    margin: 0 rem(15px);
    margin-bottom: rem(35px);

    @include phones {
      flex-direction: row;
      margin-bottom: rem(45px);
    }
  }

  &__img {
    margin-bottom: rem(20px);
  }

  &__svg {
    width: rem(97px);
    height: rem(89px);
  }

  &__desc {

    @include phones {
      text-align: left;
      margin-left: rem(30px);
    }
  }

  &__head {
    font-size: rem(24px);
    @include lifehack;
    margin-bottom: rem(15px);
    line-height: 1.25;
  }

  &__text {
    font-size: rem(14px);
    font-weight: $lightFont;
    line-height: 1.8;
  }
}

.svg-beef {
  fill: #E35028;
}

.svg-hand {
  fill: #F9B43B;
}

.svg-natural {
  fill: #8BC03F;
}

