.nav-points {
  position: fixed;
  top: rem(90px);
  right: rem(40px);
  z-index: 99;

  @include tablets {
    display: none;  
  }

  &__item {
    margin-bottom: rem(5px);
  }

  &__link {
    @include size(1rem, 1rem);
    display: block;
    position: relative;

    &::after,
    &::before {
      content: '';
      @include centered();
      border-radius: 50%;
    }

    &::after {
      @include size(rem(6px), rem(6px));
      background-color: $white;
    }

    &::before {
      @include size(0, 0);
      border: 1px solid $white;
      opacity: 0;
      transition: .2s;
    }
  
    &:hover {

      &::before {
        @include size(rem(14px), rem(14px));
        opacity: 1;
      }
    }
  }
}

.nav-points__item {

  &.is-active {

    .nav-points__link {

      &::before {
        @extend .nav-points__link:hover::before;
      }  
    }
  }
}